import axios from 'axios';
import { useContext, useState, useEffect } from 'react';
import DataContext from '../usecontext/DataContext';

export default function Showsummery() {
    const { summary } = useContext(DataContext);
    const [product, setProduct] = useState(null);
    const apiKey = "https://backenddata77720.onrender.com";

    useEffect(() => {
        if (summary?._id) {
            fetchProduct(summary._id);
        }
    }, [summary]);

    const fetchProduct = async (id) => {
        try {
            let token = localStorage.getItem("token");
            const response = await axios.get(`${apiKey}/api/purchase/stock-summary/${id}`, {
                headers: { 'authtoken': token }
            });
            setProduct(response.data);
        } catch (error) {
            console.error('Error fetching product:', error);
        }
    };

    return (
        <div className="container mx-auto p-6 bg-gray-50 min-h-screen flex items-center justify-center">
            <div className="w-full max-w-lg bg-white p-8 rounded-lg shadow-lg">
                {product ?
                    <div>
                        <h2 className="text-4xl font-extrabold mb-6 text-center text-indigo-600">
                            {product.productName}
                        </h2>
                        <div className="space-y-4">
                            <p className="text-lg">
                                <span className="font-semibold">Purchased Stock:</span> {product.purchasedStock}
                            </p>
                            <p className="text-lg">
                                <span className="font-semibold">Remaining Stock:</span> {product.remainingStock}
                            </p>
                            <p className="text-lg">
                                <span className="font-semibold">Total Sold:</span> {product.totalSold}
                            </p>
                            <p className="text-lg">
                                <span className="font-semibold">Total Amount:</span> ₹{product.totalAmount}
                            </p>
                            <p className="text-lg">
                                <span className="font-semibold">Sold Amount:</span> ₹{product.soldAmount}
                            </p>
                            <p className="text-lg">
                                <span className="font-semibold">Remaining Amount:</span> ₹{product.remainingAmount}
                            </p>
                        </div>
                    </div>: <div className="  flex   ju stify-center bg-gray-100">
      <div className="text-center p-6 bg-white rounded-lg shadow-lg">
        <img 
          src="https://img.icons8.com/ios/100/000000/empty-box.png" 
          alt="No Data Icon" 
          className="mx-auto mb-4"
        />
        <h1 className="text-2xl font-semibold text-gray-800 mb-2">No Purchase Data Available</h1>
        <p className="text-gray-500 mb-4">
          It looks like there is no purchase data to display at the moment.
        </p>
        
      </div>
    </div>
                }
            </div>
        </div>
    );
}
